.Search {
  background: rgb(255, 255, 255);
  padding: 2rem;
  background-color: rgb(255, 255, 255);
}

.Search label {
  display: block;
  font-weight: 100;
  font-size: 1.5rem;
  color: rgb(72, 73, 73);
}
.Search input {
  width: 100%;
  padding: 1rem 0 0 0;
  border: none;
  border-bottom: 1.4px solid rgb(196, 191, 191);
  font-size: 1.2rem;
  font-weight: 100;
  color: rgb(117, 115, 115);
}

.Search input:focus {
  outline: none;
}
