.User {
  display: flex;
}
.Back {
  padding: 0.5rem 1rem;
  border: none;
  background-color: teal;
  color: white;
  font-size: 1rem;
  margin-left: 1rem;
  border-radius: 1rem;
  border-top-left-radius: 100rem;
  border-bottom-left-radius: 1rem;
  margin-bottom: 1rem;
}
.Back:focus {
  outline: none;
}
.Profile {
  width: 50%;
  padding: 2rem;
}
.Profile img {
  width: 80%;
  border-radius: 50%;
}
.Data {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}
.Data h3 {
  font-size: 2rem;
  font-weight: 100;
}

.Buttons button {
  padding: 0.8rem 1rem;
  margin: 4px;
  border: none;
  background: #333;
  color: white;
  border-radius: 6px;
}
.Repo {
  padding: 1rem;
  text-align: center;
  background-color: rgb(253, 255, 253);
  border: 1px solid rgb(214, 210, 210);
  border-bottom: none;
  font-size: 1.2rem;
}
.Repo:last-child {
  border-bottom: 1px solid rgb(212, 208, 208);
  margin-bottom: 1rem;
}

@media (max-width: 700px) {
  .User {
    display: flex;
    flex-direction: column;
  }

  .Profile {
    width: 100%;
    padding: 2rem;
    box-sizing: border-box;
  }
  .Profile img {
    width: 100%;
    border-radius: 50%;
  }
  .Data {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    text-align: center;
  }
  .Buttons button {
    display: inline-block;
    width: 90%;
  }
  .Data h3 {
    margin: 0;
    margin-bottom: 10px;
  }
}
